import "./Residence.scss"
import {Avatar, Divider} from "@mui/material";
import * as React from "react";
import {Delete} from "@mui/icons-material";
import {Link} from "react-router-dom";
import {useSelector} from "react-redux";
import {BPBuildingInfo, BuildingTypeTierToChs} from "../../../components/gadgets";
import {TierToStars} from "../../../components/mappingTable";
import {getBPResidence} from "../../../utils/storage";

export default function Residence() {
    const buildingSummary = useSelector((state: any) => state.buildingSummary.data)
    const BPResidence = getBPResidence()
    const residence = buildingSummary.hideOut?.residence
    const residenceInfo = getBPResidence().find((b: any) => b.buildingId === (residence?.type === 0 ? 5 : residence?.type))
    const residenceTierInfo = residenceInfo?.tiers.find((t: any) => t.tier === (residence?.type === 0 ? 1 : residence?.tier))
//building

    return <div className={"residence-root"}>
        <div className={"residence-title"}></div>
        <div className={"residence-content"}>
            <div className={"residence-card"}>
                <img src={process.env.PUBLIC_URL + "/img/buildings/hideout_east.webp"} className={"title-img"}
                     alt={"residence-place"}/>
                <Link className={"title-banner"}
                      to={residence?.type === 0 ? `/game/district/housing` : `/game/building/${residence?.id}`}>
                    {/*<div className={"avt"}></div>*/}
                    <Avatar className={"avt"} src={residence?.avatar} sx={{width: '2.8vw', height: '2.8vw'}}
                            variant="rounded"> </Avatar>
                    <div className={"r"}>
                        <div className={""}>{residence?.type === 0 ? "" : residence?.name}</div>
                        <div className={""}>{residence?.type === 0 ? "" : BuildingTypeTierToChs(residence)}</div>
                    </div>
                </Link>
                <div className={"description"}>
                    <div className={"selector"}>
                        <span className={residence?.type === 0 ? "black" : ""}></span>/
                        <span className={residence?.type === 39 ? "black" : ""}></span>/
                        <span className={residence?.type === 37 ? "black" : ""}></span>/
                        <span className={residence?.type === 38 ? "black" : ""}></span>
                    </div>
                    <div>{residence?.type === 0 ? "" : BPBuildingInfo(residence?.type)?.description}</div>
                    <div>{residence?.type === 0 ? "" : ""}</div>
                </div>
                <Divider/>
                <div className={"status"}>
                    <div className={"sub-title"}>-<b>
                        {residence?.type === 0 ? "" : BuildingTypeTierToChs(residence)}</b>
                    </div>
                    <div className={"black-text"}><img src={process.env.PUBLIC_URL + `/img/status/statusEnergy.svg`}
                                                       className={"type-icon"}
                                                       alt={"type-icon"}/><span
                        className={"energy"}>+{residenceTierInfo?.recover.energy}</span>{residenceTierInfo?.recoverCap.energy}
                    </div>
                    <div className={"black-text"}><img src={process.env.PUBLIC_URL + `/img/status/statusHappiness.svg`}
                                                       className={"type-icon"}
                                                       alt={"type-icon"}/><span
                        className={"happiness"}>+{residenceTierInfo?.recover.happiness}</span>{residenceTierInfo?.recoverCap.happiness}
                    </div>
                    <div className={"black-text"}><img src={process.env.PUBLIC_URL + `/img/status/statusHealth.svg`}
                                                       className={"type-icon"}
                                                       alt={"type-icon"}/><span
                        className={"health"}>+{residenceTierInfo?.recover.health}</span>{residenceTierInfo?.recoverCap.health}
                    </div>
                </div>
                <div className={"status"}>
                    <div
                        className={"sub-title"}>- <b>{residence?.type === 0 ? "" : BuildingTypeTierToChs(residence)}</b>
                    </div>
                    <div className={"black-text"}><Delete className={"type-icon"}/>{residenceTierInfo?.storage}</div>
                </div>
            </div>
            {residence?.type === 0 ?
                <div className={"hint"}>
                    <Link to={`/game/district/housing`}></Link>
                    
                    <Link to={`/game/district/overview/blueprint`}></Link>
                </div>
                : null}
        </div>
    </div>
}