import {useNavigate, useParams} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {openSnackbar} from "../../../appSnackbarSlice";
import {Button, ListItemButton, TextField} from "@mui/material";
import BazaarItemSeller from "../../BazaarItems/component/BazaarItemSeller";
import BazaarItemAgent from "../../BazaarItems/component/BaazaarItemAgent";
import * as React from "react";
import BazaarBuildingSeller from "../component/BazaarBuildingSeller";
import {api_with_auth} from "../../../utils/api";
import BazaarBuildingPopover from "../component/Popover";
import Filler from "../../../components/Filler";

export default function BazaarBuildingsView(props: {}) {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const {buildingId} = useParams()
    const cityStructure = useSelector((state: any) => state.cityStructure.data)


    const [bazaarBuildings, setBazaarBuildings] = useState<any>({
            "page": 0,
            "pageSize": 20,
            "totalPages": 20,
            "totalResults": 300,
            "content": [
                {
                    //id
                    "id": 1,
                    //
                    "seller": {
                        "uid": 1,
                        "nick_name": "",
                        "avatar": "",
                        //,
                        "belong_city": 0,
                        "belong_districts": 2,
                        "belong_terrain": 7,
                    },
                    //
                    "selling": {
                        //id
                        "id": 1,
                        //
                        "name": "",
                        //
                        "avatar": "",
                        //
                        "type": 2,
                        //
                        "tier": 1,
                        //
                        "belong_city": 0,
                        //
                        "belong_districts": 0,
                        //
                        "belong_terrain": 0,
                        //
                        "stage": 2,
                        //
                        "percent": 100,
                    },
                    //
                    "buying": {
                        "id": 1,
                        "tier": 1,
                        "quantity": 1,
                    },
                    //
                    "create_time": 1,
                    //
                    "edit_time": 1,
                    //game_day
                    "available_until": 1,
                    //0123
                    "status": 0,
                },
            ]
        }
    )
    const emptyBazaarItems = bazaarBuildings.content
//fetch
    const fetchMarket = () => {
        api_with_auth("/api/bazaar/houses", "POST", {
            "page": 1,
            "pageSize": 20
        })
            .then(res => {
                    if (res.status === "success") {
                        setBazaarBuildings(res.data)
                    } else {
                        dispatch(openSnackbar({
                            "severity": "error",
                            "snackbarMessage": res.message,
                        }))
                    }
                },
            );
    }
    useEffect(() => {
        fetchMarket()
    }, [])


//
    const buyHouse = (index: number) => {
        api_with_auth("/api/bazaar/houses/" + bazaarBuildings.content[index].id + "/buy", "POST", {
            "orderId": bazaarBuildings.content[index].id,
            "buying": bazaarBuildings.content[index].buying
        })
            .then(res => {
                    if (res.status === "success") {
                        dispatch(openSnackbar({
                            "severity": "success",
                            "snackbarMessage": "",
                        }))
                        fetchMarket()
                    } else {
                        dispatch(openSnackbar({
                            "severity": "error",
                            "snackbarMessage": res.message,
                        }))
                    }
                },
            );
    }

    const [popoverOpen, setPopoverOpen] = useState(-1)

//
    if (emptyBazaarItems === null) {
        return <div className={"bazaar-items-view-root"}>
            <Filler title={""}/>
        </div>
    } else {
        return <div className={"bazaar-items-view-root"}>
            {bazaarBuildings.content?.map((sell: any, index: number) =>
                <ListItemButton className={"bazaar-item-bullet-root"} key={"bazaarItem_" + sell.id} disableRipple>
                    <div className={"bazaar-item-seller-and-others"}>
                        <BazaarBuildingSeller sell={sell}/>
                        <Button variant="text" className={"modify-order"} color={"happiness"}
                                onClick={() => setPopoverOpen(index)}
                                style={{
                                    margin: "0.8vw 0.5vw",
                                    maxHeight: '1.8vw',
                                    minHeight: '2vw',
                                    maxWidth: "4vw",
                                    minWidth: "4vw",
                                    fontSize: '0.8vw',
                                    padding: 0,
                                }}>
                            
                        </Button>
                    </div>
                    <BazaarBuildingPopover sell={sell} open={popoverOpen === index}
                                           onClose={() => setPopoverOpen(-1)}
                                           onSuccess={fetchMarket}
                    />
                </ListItemButton>
            )}
        </div>
    }
}
